import { useState, Fragment } from "react"
import { useForm } from "react-hook-form"
import { AlertCircle, ChevronLeft, Navigation, X, Check } from "react-feather"
import { Form, Input, Row, Col, Button } from "reactstrap"

import { useTranslation } from "react-i18next"
import toast from "react-hot-toast"
import Avatar from "@components/avatar"
import Spinner from "@components/spinner/Fallback-spinner"
import UILoader from "@components/ui-loader"
import { useSkin } from "@hooks/useSkin"
import useJwt from "@src/auth/jwt/useJwt"

const ToastContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="info" icon={<Navigation size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const ToastErrorContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="danger" icon={<AlertCircle size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const defaultValues = {
  address: "",
  firstName: ""
}

const EmailVerification = ({ stepper }) => {
  const { t } = useTranslation()
  const { skin } = useSkin()
  const [block, setBlock] = useState(false)
  const [errorInfo, setErrorInfo] = useState(null)
  const [successInfo, setSuccessInfo] = useState(null)
  const [otp, setOtp] = useState(["", "", "", ""])

  // ** Hooks
  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({
    defaultValues
  })

  const handleInputChange = (e, index) => {
    const newOtp = [...otp]
    newOtp[index] = e.target.value

    // Move focus to the next input box if there's an entered value
    if (e.target.value !== "" && index < 3) {
      const nextInput = document.getElementById(`otpInput${index + 1}`)
      if (nextInput) {
        nextInput.focus()
      }
    }

    setOtp(newOtp)
  }

  const handleKeyDown = (e, index) => {
    if (e.key === "Backspace" && index > 0 && otp[index] === "") {
      const newOtp = [...otp]
      newOtp[index - 1] = ""
      setOtp(newOtp)

      const prevInput = document.getElementById(`otpInput${index - 1}`)
      if (prevInput) {
        prevInput.focus()
      }
    }
  }

  const onSubmit = (data) => {
    if (otp?.every((value) => value !== "")) {
      const params = {
        otp: parseInt(otp.join()?.replaceAll(",", "")),
        register_token: sessionStorage.getItem("register_token")
      }
      setBlock(true)
      useJwt
        .registerOtp(params)
        .then((res) => {
          const data = res.data
          setBlock(false)
          setSuccessInfo(data?.message)
          sessionStorage.removeItem("register_token")
          sessionStorage.setItem("register_token_msg", data?.message)
          toast((x) => (
            <ToastContent
              x={x}
              name={t("Successfully")}
              message={data?.message}
            />
          ))
          stepper.next()
        })
        .catch((err) => {
          setBlock(false)
          let messageError = `${t(
            "Failed. Please check your email and password and make sure your account is registered correctly. Contact our support team if needed."
          )}`
          if (
            ["email_taken", "username_taken"].includes(
              err.response?.data?.error_reason
            )
          ) {
            messageError = err.response?.data?.message
          }
          setErrorInfo(messageError)
          toast((x) => (
            <ToastErrorContent
              x={x}
              name={t("Register Failed")}
              message={messageError}
            />
          ))
        })
    }
  }

  return (
    <UILoader blocking={block} loader={<Spinner />}>
      <Fragment>
        <div className="content-header mb-2">
          <h2 className="fw-bolder mb-75">{t("Email Verification")}</h2>
          <span>
            {t("Enter the OTP code that we have sent to your email")}.
          </span>
        </div>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
            {otp.map((value, index) => (
              <Col md="2" xs="3" className="mb-1">
                <Input
                  key={index}
                  id={`otpInput${index}`}
                  type="text"
                  maxLength="1"
                  value={value}
                  onChange={(e) => handleInputChange(e, index)}
                  onKeyDown={(e) => handleKeyDown(e, index)}
                  style={{
                    width: "100%",
                    height: "50px",
                    textAlign: "center",
                    fontSize: "25px",
                    fontWeight: 600
                  }}
                />
              </Col>
            ))}
            <Col md="12" className="mb-1">
              <span
                style={{
                  fontSize: "smaller",
                  color: "#999999"
                }}
              >
                {t(
                  "This OTP code has an expiration date. If your code is outdated, then repeat the registration process"
                )}
                .
              </span>
            </Col>
          </Row>

          <div className="d-flex justify-content-between mt-2">
            <Button
              color="secondary"
              className="btn-prev"
              outline
              onClick={() => stepper.previous()}
            >
              <ChevronLeft
                size={14}
                className="align-middle me-sm-25 me-0"
              ></ChevronLeft>
              <span className="align-middle d-sm-inline-block d-none">
                {t("Previous")}
              </span>
            </Button>
            {otp?.every((value) => value !== "") && (
              <Button type="submit" color="success" className="btn-next">
                <span className="align-middle d-sm-inline-block d-none">
                  {t("Submit")}
                </span>
                <Check size={14} className="align-middle ms-sm-25 ms-0"></Check>
              </Button>
            )}
          </div>
        </Form>
      </Fragment>
    </UILoader>
  )
}

export default EmailVerification
