import { useState, Fragment } from "react"
import useJwt from "@src/auth/jwt/useJwt"
import * as yup from "yup"
import { useForm, Controller } from "react-hook-form"
import { yupResolver } from "@hookform/resolvers/yup"
import { AlertCircle, Navigation, X, ChevronRight } from "react-feather"
import { Form, Label, Input, Row, Col, Button, FormFeedback } from "reactstrap"
import InputPasswordToggle from "@components/input-password-toggle"
import { useTranslation } from "react-i18next"
import toast from "react-hot-toast"
import Avatar from "@components/avatar"
import Spinner from "@components/spinner/Fallback-spinner"
import UILoader from "@components/ui-loader"
import { useSkin } from "@hooks/useSkin"

const ToastContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="info" icon={<Navigation size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const ToastErrorContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="danger" icon={<AlertCircle size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const defaultValues = {
  email: "",
  username: "",
  password: "",
  confirmPassword: ""
}

const AccountDetails = ({ stepper }) => {
  const { t } = useTranslation()
  const { skin } = useSkin()
  const [block, setBlock] = useState(false)
  const [errorInfo, setErrorInfo] = useState(null)
  const [successInfo, setSuccessInfo] = useState(null)

  const SignupSchema = yup.object().shape({
    username: yup
      .string()
      .required("Username is required")
      .min(4, "Username must be at least 4 characters")
      .max(15, "Username cannot exceed 15 characters")
      .matches(
        /^[a-zA-Z0-9_]+$/,
        "Username can only contain letters, numbers, and underscores"
      ),
    email: yup.string().email().required(),
    password: yup.string().required(),
    confirmPassword: yup
      .string()
      .required()
      .oneOf([yup.ref(`password`), null], t("Passwords must match"))
  })

  // ** Hooks

  const {
    control,
    handleSubmit,
    formState: { errors }
  } = useForm({
    defaultValues,
    resolver: yupResolver(SignupSchema)
  })

  const onSubmit = (data) => {
    if (Object.values(data).every((field) => field.length > 0)) {
      let params = { ...data }
      if (localStorage.getItem("dev") !== null) {
        params["device_id"] = localStorage.getItem("dev")
        delete params["confirmPassword"]
      }
      setBlock(true)
      useJwt
        .register(params)
        .then((res) => {
          const data = res.data
          setBlock(false)
          setSuccessInfo(data?.message)
          sessionStorage.setItem("register_token", data?.register_token)
          toast((x) => (
            <ToastContent
              x={x}
              name={t("Successfully")}
              message={data?.message}
            />
          ))
          stepper.next()
        })
        .catch((err) => {
          // console.log(err.response?.data?.message)
          setBlock(false)
          // console.log(err)
          let messageError = `${t(
            "Failed. Please check your email and password and make sure your account is registered correctly. Contact our support team if needed."
          )}`
          if (
            ["email_taken", "username_taken", "email_await"].includes(
              err.response?.data?.error_reason
            )
          ) {
            messageError = err.response?.data?.message
          }
          setErrorInfo(messageError)
          toast((x) => (
            <ToastErrorContent
              x={x}
              name={t("Register Failed")}
              message={messageError}
              // message={`${t(err.message)}`}
            />
          ))
        })
    }
  }

  return (
    <UILoader blocking={block} loader={<Spinner />}>
      <Fragment>
        <div className="content-header mb-2">
          <h2 className="fw-bolder mb-75">{t("Account Information")}</h2>
          <span>"{t("Enter your email password details")}"</span>
        </div>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
            <Col md="6" className="mb-1">
              <Label className="form-label" for={`email`}>
                Email
              </Label>
              <Controller
                control={control}
                id="email"
                name="email"
                render={({ field }) => (
                  <Input
                    type="email"
                    placeholder="john.doe@email.com"
                    invalid={errors.email && true}
                    {...field}
                  />
                )}
              />
              {errors.email && (
                <FormFeedback>{errors.email.message}</FormFeedback>
              )}
            </Col>
            <Col md="6" className="mb-1">
              <Label className="form-label" for="username">
                Username
              </Label>
              <Controller
                id="username"
                name="username"
                control={control}
                render={({ field }) => (
                  <Input
                    placeholder="johndoe"
                    invalid={errors.username && true}
                    {...field}
                  />
                )}
              />
              {errors.username && (
                <FormFeedback>{errors.username.message}</FormFeedback>
              )}
            </Col>
          </Row>
          <Row>
            <div className="form-password-toggle col-md-6 mb-1">
              <Controller
                id="password"
                name="password"
                control={control}
                render={({ field }) => (
                  <InputPasswordToggle
                    label={t("Password")}
                    htmlFor="password"
                    className="input-group-merge"
                    invalid={errors.password && true}
                    {...field}
                  />
                )}
              />
              {errors.password && (
                <FormFeedback>{errors.password.message}</FormFeedback>
              )}
            </div>
            <div className="form-password-toggle col-md-6 mb-1">
              <Controller
                control={control}
                id="confirmPassword"
                name="confirmPassword"
                render={({ field }) => (
                  <InputPasswordToggle
                    label={t("Confirm Password")}
                    htmlFor="confirmPassword"
                    className="input-group-merge"
                    invalid={errors.confirmPassword && true}
                    {...field}
                  />
                )}
              />
              {errors.confirmPassword && (
                <FormFeedback>{errors.confirmPassword.message}</FormFeedback>
              )}
            </div>
          </Row>
          <Row>
            <Col md="6" className="mb-1">
              <Label className="form-label" for="name">
                {t("Name")}
              </Label>
              <Controller
                id="name"
                name="name"
                control={control}
                render={({ field }) => (
                  <Input
                    placeholder="John"
                    invalid={errors.name && true}
                    {...field}
                  />
                )}
              />
              {errors.name && (
                <FormFeedback>{errors.name.message}</FormFeedback>
              )}
            </Col>
            <Col md="6" className="mb-1">
              <Label className="form-label" for="mobile_number">
                {t("Mobile Number")}
              </Label>
              <Controller
                id="mobile_number"
                name="mobile_number"
                control={control}
                render={({ field }) => (
                  <Input
                    type="number"
                    id="phone"
                    name="mobile_number"
                    placeholder="(472) 765-3654"
                    invalid={errors.mobile_number && true}
                    {...field}
                  />
                )}
              />
              {errors.mobile_number && (
                <FormFeedback>{errors.mobile_number.message}</FormFeedback>
              )}
            </Col>
          </Row>
          {/* <Row>
          <Col sm={12} className="mb-1">
            <Label className="form-label" for="profile-link">
              Profile Link
            </Label>
            <Input id="profile-link" placeholder="johndoe/profile" />
          </Col>
          <Col sm={12} className="mb-1">
            <div className="form-check form-check-inline">
              <Input type="checkbox" id="remember-me" />
              <Label for="remember-me" className="form-check-label">
                {t("Remember Me")}
              </Label>
            </div>
          </Col>
        </Row> */}
          <div className="d-flex justify-content-end mt-2">
            {/* <Button color="secondary" className="btn-prev" outline disabled>
            <ChevronLeft
              size={14}
              className="align-middle me-sm-25 me-0"
            ></ChevronLeft>
            <span className="align-middle d-sm-inline-block d-none">
              {t("Previous")}
            </span>
          </Button> */}
            <Button type="submit" color="primary" className="btn-next">
              <span className="align-middle d-sm-inline-block d-none">
                {t("Next")}
              </span>
              <ChevronRight
                size={14}
                className="align-middle ms-sm-25 ms-0"
              ></ChevronRight>
            </Button>
          </div>
        </Form>
      </Fragment>
    </UILoader>
  )
}

export default AccountDetails
