///GET DOMAIN INFO
const ToastContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="info" icon={<Navigation size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const ToastErrorContent = ({ x, name, message }) => {
  return (
    <div className="d-flex">
      <div className="me-1">
        <Avatar size="sm" color="danger" icon={<AlertCircle size={12} />} />
      </div>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <h6>{name}</h6>
          <X
            size={12}
            className="cursor-pointer"
            onClick={() => toast.dismiss(x.id)}
          />
        </div>
        <span>{message}</span>
      </div>
    </div>
  )
}

const RegisterMultiSteps = () => {
  const storeDomainClaims = useSelector((state) => state.domain_claims)
  const { t } = useTranslation()
  const { skin } = useSkin()
  const dispatch = useDispatch()
  const ability = useContext(AbilityContext)
  const navigate = useNavigate()
  const [visible, setVisible] = useState(true)
  const toggleInfoVisible = () => setVisible(!visible)
  const defLogo = require("@src/assets/images/logo/logo.svg").default
  const [searchParams] = useSearchParams()
  const [block, setBlock] = useState(false)
  const [errorInfo, setErrorInfo] = useState(null)
  const [successInfo, setSuccessInfo] = useState(null)
  const [logo, setLogo] = useState(defLogo)
  const [unitLogo, setUnitLogo] = useState(null)

  // ** Ref
  const ref = useRef(null)

  // ** State
  const [stepper, setStepper] = useState(null)

  const steps = [
    {
      id: "account-details",
      title: t("Account"),
      subtitle: t("Enter email"),
      icon: <Home size={18} />,
      content: <AccountDetails stepper={stepper} />
    },
    {
      id: "email-verification",
      title: t("Verification"),
      subtitle: t("Enter Verification"),
      icon: <CheckCircle size={18} />,
      content: <EmailVerification stepper={stepper} />
    },
    {
      id: "complete-info",
      title: t("Complete"),
      subtitle: t("Result Information"),
      icon: <Smile size={18} />,
      content: <PersonalInfo stepper={stepper} />
    }
    // {
    //   title: "Billing",
    //   id: "step-billing",
    //   subtitle: "Payment Details",
    //   icon: <CreditCard size={18} />,
    //   content: <Billing stepper={stepper} />
    // }
  ]

  const source = require("@src/assets/images/pages/create-account.svg").default

  if (!localStorage.getItem("i18nextLng")) {
    localStorage.setItem("i18nextLng", "id")
  } else if (localStorage["i18nextLng"] === "id-ID") {
    localStorage.setItem("i18nextLng", "id")
  }

  localStorage.removeItem("domainClaim")
  localStorage.removeItem("userData")
  localStorage.removeItem("accessToken")
  localStorage.removeItem("refreshToken")
  sessionStorage.clear()

  const {
    control,
    setError,
    handleSubmit,
    getValues,
    reset,
    formState: { errors, isDirty, isValid }
  } = useForm({
    mode: "all"
  })

  const useMediaQuery = (query) => {
    const mediaMatch = window.matchMedia(query)
    const [matches, setMatches] = useState(mediaMatch.matches)

    useEffect(() => {
      const handler = (e) => setMatches(e.matches)
      mediaMatch.addListener(handler)
      return () => mediaMatch.removeListener(handler)
    })
    return matches
  }

  const isLargeScreen = useMediaQuery("(min-width: 500px)")

  const BgImg = (storeDomainClaims) => {
    return (
      storeDomainClaims.data?.unit?.unit_app_attributes?.background_img ??
      storeDomainClaims.data?.app?.logo?.background_img
    )
  }

  const BgOrVideoImgOverlay = (storeDomainClaims) => {
    return (
      storeDomainClaims.data?.unit?.unit_app_attributes
        ?.background_video_overlay ??
      storeDomainClaims.data?.app?.setting?.background_video_overlay ??
      storeDomainClaims.data?.unit?.unit_app_attributes
        ?.background_img_overlay ??
      storeDomainClaims.data?.app?.setting?.background_img_overlay
    )
  }

  useEffect(() => {
    if (storeDomainClaims?.data?.app?.info) {
      const logoResp = storeDomainClaims.data?.app?.logo
      const unitLogox = storeDomainClaims.data?.unit?.attributes?.logo
      const app_setting = {
        ...{
          layout:
            storeDomainClaims.data?.unit?.unit_app_attributes?.layout ?? {}
        },
        ...(storeDomainClaims?.data?.app?.setting ?? {})
      }
      const unit_app_attributes =
        storeDomainClaims?.data?.unit?.unit_app_attributes
      const unit_app_attributes_setting = unit_app_attributes?.setting

      if (logoResp?.appLogo) {
        setLogo(logoResp.appLogo)
      }
      if (app_setting?.layout?.skin || app_setting.skin) {
        localStorage.setItem(
          "skin",
          `"${app_setting.layout.skin ?? app_setting.skin}"`
        )
        localStorage.setItem(
          "skinDef",
          `"${app_setting.layout.skin ?? app_setting.skin}"`
        )
      }
      if (app_setting?.layout?.contentWidth || app_setting?.contentWidth) {
        localStorage.setItem(
          "contentWidth",
          `"${app_setting.layout.contentWidth ?? app_setting?.contentWidth}"`
        )
      }

      if (unitLogox) {
        setUnitLogo(unitLogox)
      }
    }
  }, [])

  return (
    storeDomainClaims?.data &&
    (storeDomainClaims?.data?.app?.setting?.enable_registration ||
      storeDomainClaims?.data?.unit?.unit_app_attributes
        ?.enable_registration) && (
      <div className="auth-wrapper auth-cover">
        <Row className="auth-inner m-0">
          <Link
            className="brand-logo d-flex align-items-center"
            // style={{ position: "inherit" }}
            to="/"
            onClick={(e) => e.preventDefault()}
          >
            {(storeDomainClaims?.data.app?.logo?.appLogo ||
              storeDomainClaims.data?.app?.logo?.appLogoLogin ||
              storeDomainClaims.data?.app?.logo?.appLogoLoginBig ||
              storeDomainClaims.data?.unit?.unit_app_attributes ||
              storeDomainClaims.data?.unit?.unit_app_attributes?.appLogoLogin
                ?.appLogoLoginBig) && (
              <img
                height={28}
                style={{
                  maxWidth: "230px",
                  maxHeight: "60px",
                  height: "auto"
                }}
                src={
                  storeDomainClaims.data?.unit?.unit_app_attributes
                    ?.appLogoLoginBig ??
                  storeDomainClaims.data?.unit?.unit_app_attributes
                    ?.appLogoLogin ??
                  storeDomainClaims.data?.app?.logo?.appLogoLoginBig ??
                  storeDomainClaims.data?.app?.logo?.appLogoLogin ??
                  storeDomainClaims.data.app?.logo?.appLogo
                }
                alt="logo"
              />
            )}
            {storeDomainClaims.data.app?.name &&
              !storeDomainClaims.data?.app?.logo?.appLogoLoginBig && (
                <h2 className="brand-text text-primary ms-1 mt-50">
                  {storeDomainClaims.data.app.name}
                </h2>
              )}
          </Link>
          <Col lg="3" className="d-none d-lg-flex align-items-center p-0">
            <div className="w-100 d-lg-flex align-items-center justify-content-center">
              <img className="img-fluid w-100" src={source} alt="Login Cover" />
            </div>
          </Col>
          <Col
            lg="9"
            className="d-flex align-items-center auth-bg px-2 px-sm-3 px-lg-5 pt-3"
          >
            <div className="width-700 mx-auto">
              <Wizard
                ref={ref}
                steps={steps}
                instance={(el) => setStepper(el)}
                headerClassName="px-0"
                contentWrapperClassName="px-0 mt-4"
                className="register-multi-steps-wizard shadow-none"
              />
            </div>
          </Col>
        </Row>
      </div>
    )
  )
}

export default RegisterMultiSteps
